.page-header {
  position: relative;
  display: grid;
  grid-template-columns: 15% auto;
  grid-template-rows: auto;
  overflow: hidden;

  background-color: #fff;

  border: 1px solid #eeeeee;

  grid-template-areas: "logo wrapper";

  @media (max-width: 850px) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: auto;
    grid-template-areas:
      "logo"
      "wrapper";
  }

  &__wrapper {
    grid-area: wrapper;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 2;

    grid-template-areas:
      "nav-box nav-box nav-box langs  contacts"
      "links links  search search    contacts";

    @media (max-width: 1200px) {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: 3;
      grid-template-areas:
        " langs  contacts contacts"
        " nav-box nav-box nav-box"
        "links      search search";
    }

    @media (max-width: 850px) {
      z-index: 2;
      position: fixed;
      top: 72px;
      left: 0;
      width: 100%;
      height: calc(100vh - 72px);
      overflow-y: scroll;
      overflow-x: hidden;

      background-color: #ffffff;

      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: auto;
      grid-template-areas:
        "langs"
        "contacts"
        "nav-box"
        "links";
    }

    & > * {
      border: 1px solid #eeeeee;
      border-top: 0px;
      margin: 0 -1px -1px 0;
    }
  }

  &__logo {
    grid-area: logo;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (max-width: 850px) {
      align-items: start;
      padding: 10px;
      border-bottom: 1px solid #eeeeee;
    }
  }

  &__phone-and-email {
    grid-area: contacts;

    display: grid;

    @media (max-width: 1200px) {
      display: flex;
      flex-wrap: wrap;
    }

    @media (max-width: 850px) {
      flex-direction: column;
      text-align: center;
      border-right: 0;
      border-left: 0;
    }
  }

  &__nav {
    grid-area: nav-box;

    @media (max-width: 850px) {
      padding: 0 20px;
      border-right: 0;
      border-left: 0;
    }
  }

  &__langs {
    grid-area: langs;

    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 850px) {
      text-align: center;
      border-right: 0;
      border-left: 0;
      padding: 10px;
    }
  }

  &__links {
    grid-area: links;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    @media (max-width: 850px) {
      flex-direction: column;
      border-right: 0;
      border-left: 0;
    }

    & a {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      padding: 10px;
      width: 50%;
      height: 100%;
      color: $c-black;

      &:not(:last-child) {
        border-right: 1px solid #eeeeee;

        @media (max-width: 850px) {
          border-right: 0;
        }
      }

      &::before {
        z-index: -1;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        background-color: #1c8a43;
        transition: 0.5s;
      }

      @media (max-width: 850px) {
        width: 100%;
        height: auto;
        text-align: center;
      }

      &:hover {
        color: #ffffff;
        &::before {
          height: 100%;
        }

        & svg {
          fill: #ffffff;
        }
      }

      & svg {
        fill: $c-black;
        width: 20px;
        height: 20px;
      }
    }
  }

  &__email {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $c-black;

    &:focus,
    &:hover {
      color: #1c8a43;
    }

    @media (max-width: 1200px) {
      padding: 10px;
      width: 50%;
    }

    @media (max-width: 850px) {
      width: 100%;
      height: 50%;
    }
  }

  &__phone {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $c-black;
    border-bottom: 1px solid #eeeeee;

    &:focus,
    &:hover {
      color: #1c8a43;
    }

    @media (max-width: 1200px) {
      padding: 10px;
      width: 50%;
      border-bottom: 0;
      border-right: 1px solid #eeeeee;
    }

    @media (max-width: 850px) {
      width: 100%;
      height: 50%;
      border-right: 0;
      border-bottom: 1px solid #eeeeee;
    }
  }

  &__search {
    grid-area: search;

    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 850px) {
      display: none;
    }

    &--mob {
      display: none;

      @media (max-width: 850px) {
        display: flex;
        position: absolute;
        top: 10px;
        right: 0;
      }
    }
  }

  &__toggle {
    @include btn-reset;
    display: none;

    @media (max-width: 850px) {
      display: block;
      position: absolute;
      top: 10px;
      right: 0;

      width: 50px;
      height: 50px;
    }

    &.active {
      & svg {
        width: 29px;
        height: 29px;

        & rect {
          fill: $c-dark;
          width: 100%;

          &:nth-child(2) {
            opacity: 0;
          }

          &:first-child {
            transform: rotate(45deg) translate(-3px, -8px);
          }
          &:last-child {
            transform: rotate(-45deg) translate(-8px, 0);
          }
        }
      }
    }

    & svg {
      & rect {
        fill: #b1afa9;

        transition: 0.3s;

        .scroll-up & {
          fill: $c-dark;
        }
      }
    }
  }
}

.page-header__wrapper.js-menu-open {
  @media (max-width: 850px) {
    display: none;
  }
}
.page-header__wrapper.js-menu-close {
  @media (max-width: 850px) {
    display: none;
  }
}
