  height: auto;
  max-width: 100%;
  vertical-align: top; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
