.overflow {
  overflow: hidden; }

.page {
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 30px;
  color: #292929;
  background-color: #f7f5f1;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

#map {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  min-height: 500px; }

.page-header {
  position: relative;
  display: grid;
  grid-template-columns: 15% auto;
  grid-template-rows: auto;
  overflow: hidden;
  background-color: #fff;
  border: 1px solid #eeeeee;
  grid-template-areas: "logo wrapper"; }
  @media (max-width: 850px) {
    .page-header {
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: auto;
      grid-template-areas: "logo" "wrapper"; } }
  .page-header__wrapper {
    grid-area: wrapper;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 2;
    grid-template-areas: "nav-box nav-box nav-box langs  contacts" "links links  search search    contacts"; }
    @media (max-width: 1200px) {
      .page-header__wrapper {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 3;
        grid-template-areas: " langs  contacts contacts" " nav-box nav-box nav-box" "links      search search"; } }
    @media (max-width: 850px) {
      .page-header__wrapper {
        z-index: 2;
        position: fixed;
        top: 72px;
        left: 0;
        width: 100%;
        height: calc(100vh - 72px);
        overflow-y: scroll;
        overflow-x: hidden;
        background-color: #ffffff;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: auto;
        grid-template-areas: "langs" "contacts" "nav-box" "links"; } }
    .page-header__wrapper > * {
      border: 1px solid #eeeeee;
      border-top: 0px;
      margin: 0 -1px -1px 0; }
  .page-header__logo {
    grid-area: logo;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    @media (max-width: 850px) {
      .page-header__logo {
        align-items: start;
        padding: 10px;
        border-bottom: 1px solid #eeeeee; } }
  .page-header__phone-and-email {
    grid-area: contacts;
    display: grid; }
    @media (max-width: 1200px) {
      .page-header__phone-and-email {
        display: flex;
        flex-wrap: wrap; } }
    @media (max-width: 850px) {
      .page-header__phone-and-email {
        flex-direction: column;
        text-align: center;
        border-right: 0;
        border-left: 0; } }
  .page-header__nav {
    grid-area: nav-box; }
    @media (max-width: 850px) {
      .page-header__nav {
        padding: 0 20px;
        border-right: 0;
        border-left: 0; } }
  .page-header__langs {
    grid-area: langs;
    display: flex;
    justify-content: center;
    align-items: center; }
    @media (max-width: 850px) {
      .page-header__langs {
        text-align: center;
        border-right: 0;
        border-left: 0;
        padding: 10px; } }
  .page-header__links {
    grid-area: links;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; }
    @media (max-width: 850px) {
      .page-header__links {
        flex-direction: column;
        border-right: 0;
        border-left: 0; } }
    .page-header__links a {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      padding: 10px;
      width: 50%;
      height: 100%;
      color: #292929; }
      .page-header__links a:not(:last-child) {
        border-right: 1px solid #eeeeee; }
        @media (max-width: 850px) {
          .page-header__links a:not(:last-child) {
            border-right: 0; } }
      .page-header__links a::before {
        z-index: -1;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        background-color: #1c8a43;
        transition: 0.5s; }
      @media (max-width: 850px) {
        .page-header__links a {
          width: 100%;
          height: auto;
          text-align: center; } }
      .page-header__links a:hover {
        color: #ffffff; }
        .page-header__links a:hover::before {
          height: 100%; }
        .page-header__links a:hover svg {
          fill: #ffffff; }
      .page-header__links a svg {
        fill: #292929;
        width: 20px;
        height: 20px; }
  .page-header__email {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #292929; }
    .page-header__email:focus, .page-header__email:hover {
      color: #1c8a43; }
    @media (max-width: 1200px) {
      .page-header__email {
        padding: 10px;
        width: 50%; } }
    @media (max-width: 850px) {
      .page-header__email {
        width: 100%;
        height: 50%; } }
  .page-header__phone {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #292929;
    border-bottom: 1px solid #eeeeee; }
    .page-header__phone:focus, .page-header__phone:hover {
      color: #1c8a43; }
    @media (max-width: 1200px) {
      .page-header__phone {
        padding: 10px;
        width: 50%;
        border-bottom: 0;
        border-right: 1px solid #eeeeee; } }
    @media (max-width: 850px) {
      .page-header__phone {
        width: 100%;
        height: 50%;
        border-right: 0;
        border-bottom: 1px solid #eeeeee; } }
  .page-header__search {
    grid-area: search;
    display: flex;
    justify-content: center;
    align-items: center; }
    @media (max-width: 850px) {
      .page-header__search {
        display: none; } }
    .page-header__search--mob {
      display: none; }
      @media (max-width: 850px) {
        .page-header__search--mob {
          display: flex;
          position: absolute;
          top: 10px;
          right: 0; } }
  .page-header__toggle {
    padding: 0;
    margin: 0;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    display: none; }
    .page-header__toggle:focus {
      outline: none; }
    .page-header__toggle:hover {
      outline: none; }
    @media (max-width: 850px) {
      .page-header__toggle {
        display: block;
        position: absolute;
        top: 10px;
        right: 0;
        width: 50px;
        height: 50px; } }
    .page-header__toggle.active svg {
      width: 29px;
      height: 29px; }
      .page-header__toggle.active svg rect {
        fill: #131217;
        width: 100%; }
        .page-header__toggle.active svg rect:nth-child(2) {
          opacity: 0; }
        .page-header__toggle.active svg rect:first-child {
          transform: rotate(45deg) translate(-3px, -8px); }
        .page-header__toggle.active svg rect:last-child {
          transform: rotate(-45deg) translate(-8px, 0); }
    .page-header__toggle svg rect {
      fill: #b1afa9;
      transition: 0.3s; }
      .scroll-up .page-header__toggle svg rect {
        fill: #131217; }

@media (max-width: 850px) {
  .page-header__wrapper.js-menu-open {
    display: none; } }

@media (max-width: 850px) {
  .page-header__wrapper.js-menu-close {
    display: none; } }

.nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 850px) {
    .nav__list {
