.page {
  position: relative;

  min-height: 100%;

  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 30px;
  color: $c-black;

  background-color: #f7f5f1;
  scroll-behavior: smooth;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#map {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  min-height: 500px;
  // background-color: darkcyan;
}
