@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 0 30px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 0 30px;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 0 30px;
  }
}

@mixin list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin btn-reset {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;

  &:focus {
    outline: none;
  }

  &:hover {
    outline: none;
  }
}

@mixin hover {
  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

@mixin center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

$html-font-size: 16px;

@function stripUnit($value) {
  @return $value / ($value * 0 + 1);
}

@function rem($pxValue) {
  @return #{stripUnit($pxValue) / stripUnit($html-font-size)}rem;
}


@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(
          #{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} *
            ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})
        );
      }

      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size;
      }
    }
  }
}

@mixin heroTitle {
  position: relative;
  font-family: $font-title;
  font-style: normal;
  font-weight: 600;
  font-size: 50.9091px;
  line-height: 62px;

  text-align: center;
  letter-spacing: 12px;
  text-transform: uppercase;
  color: $c-light;

  @include fluid-type(420px, 1920px, 32px, 50.9091px);

  @media (max-width: $mobile) {
    font-size: 32px;
    line-height: 39px;
    letter-spacing: 1.5px;
  }
}

@mixin titleNoColor {
  position: relative;
  padding-top: 20px;
  font-family: $font-title;
  font-style: normal;
  font-weight: 500;
  // font-size: 38px;
  line-height: 46px;

  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;

  @include fluid-type(420px, 1920px, 28px, 38px);

  @media (max-width: $mobile) {
    // font-size: 28px;
    line-height: 34px;
  }
}

@mixin lineBeforeButtom {
  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 127px;
    height: 3px;
    background-color: #ccbb9e;
  }
}

@mixin parallax {
  transform: translateZ(-1px) scale(1.5);
  z-index: -1;
}
