.nav {
  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 850px) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      align-items: start;
      height: 100%;
      overflow-y: scroll;
    }
  }

  &__link {
    display: block;
    padding: 10px;
    color: $c-black;

    @include hover;
  }
}
