.search {
  position: relative;
  width: 100%;
  height: 100%;

  & label {
    width: 100%;
    height: 100%;

    @media (max-width: 850px) {
      display: none;
    }

    & input {
      width: 100%;
      height: 100%;
      padding: 10px;
      border: 1px solid #eeeeee;
      border-radius: 0;
      outline: none;
    }
  }

  & button {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 99%;
    min-width: 50px;

    @include btn-reset;

    background-color: #f9f9f9;

    @media (max-width: 850px) {
      right: 50px;
      height: 50px;
      top: 0;
    }
  }
}
